import React from "react";
import s from './Post.module.css';

type PostType = {
    message: string
    likesCount: number
}
const Post = (props: PostType) => {

    return (
        <div className={s.item}>
            <img
                src={'data:image/jpeg;base64,'}/>
            {props.message}

            <div>
                {props.likesCount}
                <span> like</span>
            </div>
        </div>

    );
}

export default Post